
.header {
  position: relative;
  color: #fff;
  overflow: hidden;
}
.header .content-wrapper {
  position: relative;
  padding: 46px 12px 60px 24px;
  font-size: 0;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .content {
  display: inline-block;
  font-size: 14px;
