}
.header .content-wrapper .content .support .text {
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .supports-count {
  position: absolute;
  right: 12px;
  bottom: 14px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  background: rgba(0,0,0,0.2);
  text-align: content;
}
.header .content-wrapper .supports-count .count {
  font-size: 10px;
  vertical-align: top;
}
.header .content-wrapper .supports-count .icon {
  margin-left: 2px;
  line-height: 24px;
  font-size: 10px;
}
.header .bulletin-wrapper {
  position: relative;
  background-color: rgba(255,255,255,0.5);
  height: 28px;
  line-height: 30px;
  padding: 0 22px 0 12px;
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.header .bulletin-wrapper .icon {
  position: absolute;
  font-size: 20px;
  right: 12px;
  top: 0px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-backdrop-filter: blur(10px);
}
.header .detail.fade-enter-active {
  animation: bounce-in 0.5s;
}
.header .detail.fade-leave-active {
  animation: bounce-out 0.5s;
}
@keyframes bounce-in {
0% {
    transform: scale(0);
}
50% {
    transform: scale(1.5);
}
100% {
    transform: scale(1);
}
}
@keyframes bounce-out {
0% {
    transform: scale(1);
}
50% {
    transform: scale(1.5);
}
100% {
    transform: scale(0);
}
}
