
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
}
.food.fade-enter-active,
.food.fade-leave-active {
  transition: all 0.2s linear;
  transform: translate3d(0, 0, 0);
}
.food.fade-enter,
.food.fade-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.food .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .image-header img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.food .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .image-header .back .iconfont {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  position: relative;
  padding: 18px;
}
.food .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
  height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  display: inline-block;
  color: #93999f;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  font-weight: 700px;
  line-height: 24px;
}
.food .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food .content .price .old {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.food .content .cartControl-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
}
.food .content .buy.buy-enter-active,
.food .content .buy.buy-leave-active {
  transition: all 0.2s;
  opacity: 0;
}
.food .content .buy.buy-enter,
.food .content .buy.buy-leave-active {
  opacity: 0;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  font-size: 12px;
  line-height: 24px;
  padding: 0 8px;
  color: #4d555d;
}
.food .rating {
  padding-top: 18px;
}
.food .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
}
.food .rating .rating-wrapper {
  padding: 0 18px;
}
.food .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(1,17,27,0.1);
  width: 100%;
  content: '';
}
.food .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.food .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .rating .rating-wrapper .rating-item .text .iconfont {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food .rating .rating-wrapper .rating-item .text .icon-damuzhi {
  color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon-down {
  color: #93999f;
}
.food .rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


