
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  position: relative;
  font-size: 0;
}
.ratingselect .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  width: 100%;
  content: '';
}
.ratingselect .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  font-size: 12px;
  color: #4d555d;
}
.ratingselect .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block.positive.active {
  color: #fff;
  background: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.negative.active {
  color: #fff;
  background: #4d555d;
}
.ratingselect .rating-type .block .count {
  font-size: 8px;
  margin-left: 2px;
  line-height: 16px;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  font-size: 12px;
  color: #93999f;
  border: 1px solid rgba(1,17,27,0.1);
  font-size: 0;
}
.ratingselect .switch.on .iconfont {
  color: #00c850;
}
.ratingselect .switch .iconfont {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
  margin-right: 4px;
}
.ratingselect .switch .text {
  font-size: 12px;
}


